.bkt-main {
    .bkt-main-title {
        font-weight: 700;
        margin: 52px 0;
    }

    .bkt-main-search {
        position: relative;
        padding: 15px;
        margin-right: 0;

        & .bkt-main-search__input {
            border: none;
            outline: none;
            box-shadow: none;
            padding: 0 15px;
        }

        & .bkt-button {
            font-size: $font-size-14;
        }
    }

    .bkt-main-categories .bkt-card__row {
        width: 370px;

        &__title {
            font-size: 18px;
            font-family: $font-family-sans-serif;
            color: rgb(45, 50, 68);
            font-weight: bold;
            line-height: 1.222;
        }

        &__subtitle {
            font-size: 14px;
            font-family: $font-family-sans-serif;
            line-height: 1.2;
        }
    }

    .bkt-main-statistic {
        gap: 30px;

        & .bkt-card {
            height: 100px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            &__title {
                color: white;
            }

            &__text {
                color: rgb(255, 255, 255);
            }

            &.bkt-bg-red-light .bkt-card__background-figure-1 {
                background: #f2bbb3;
            }

            &.bkt-bg-yellow .bkt-card__background-figure-1 {
                background: #f8e8b8;
            }

            &.bkt-bg-blue .bkt-card__background-figure-1 {
                background: #aedffe;
            }

            & .bkt-card__background-figure-1 {
                position: absolute;
                margin: auto;
                top: 7px;
                width: 92%;
                height: 100px;
                z-index: -1;
                left: 4.5%;
                right: 4.5%;
            }

            & .bkt-card__background-figure-2 {
                position: absolute;
                margin: auto;
                top: 14px;
                width: 81%;
                height: 100px;
                z-index: -2;
                opacity: 0.15;
                left: 10.2%;
                right: 10.2%;
            }
        }
    }

    .bkt-main-instruments {
        margin-bottom: 10px;
    }

    .bkt-main-filters {
        background: white;
        //background: $main-default;
        align-items: center;
        //justify-content: space-between;
        padding: 20px;
        flex-wrap: wrap;
        margin: 38px 0 0 0;

        & .bkt-select__wrapper {
            width: 100%;
        }

        & .bkt-select {
            border-radius: 15px;
            max-width: 374px;
            width: 100%;
        }

        & .bkt-check {
            margin-bottom: 8px;
        }

        & .bkt-check__list {
            flex-flow: column;
        }

        & .bkt-check__wrapper {
            margin-left: 25px;
        }

        & .bkt-check__input {
            border: 2px solid $neutral-light;
            background-color: white;
            outline: none;
            box-shadow: none;
            margin-right: 8px;
            border-radius: 6px;
            width: 24px;
            height: 24px;

        }

        //& input:checked ~ .bkt-check__input-check {
        //    background: $white;
        //    box-shadow: 0 3px 10px 0 rgba(255, 255, 255, 0.53);
        //}

        & .bkt-check__label {
            color: $main-default;
            line-height: 1.833;
            font-size: $font-size-12;
        }
    }

