.bkt-monitoring {
    min-height: 50vh;
    gap: 30px;
    display: flex;
    flex-direction: column;

    &__title {
        margin-bottom: 50px;
        margin-top: 50px;

        & span {
            font-weight: 800;
            font-size: 30px;
        }
    }

    &__paths-list {
        //padding: 0 20px;
        width: calc(100% - 80px);

        & .bkt-monitoring__path:first-child {
            padding: 12px 30px 12px 30px;
        }

        & .bkt-monitoring__path {
            margin-right: 10px;
            border-radius: 20px;
            height: 60px;
            padding: 12px 12px 12px 30px;
            font-size: 14px;
            font-weight: 600;
            line-height: 14px;
            flex-wrap: nowrap;
            display: flex;
            align-items: center;
            gap: 25px;
            text-transform: uppercase;
            color: white;
        }
    }

    & .bkt-button__new-monitoring {
        float: right;
        border-radius: 20px;
        font-size: 14px;
        line-height: 14px;
        min-height: 60px;
    }

    & .bkt-monitoring__paths-collapse {
        width: 100%;
        background-color: transparent;
        gap: 10px;

        & .bkt-collapse__header {
            text-transform: uppercase;
            color: white;
            text-align: center;
            padding: 5px;
            border-radius: 13px;
            min-height: 44px;

            & h6 {
                //font-size: 10px;
                font-family: $font-family-sans-serif;
                color: rgb(254, 254, 254);
                font-weight: 600;
                text-transform: uppercase;
                line-height: 2;
            }
        }

        & .bkt-collapse__body {
            background-color: transparent;
            padding: 0;
            row-gap: 10px;
            max-height: 200px;
            overflow-y: scroll;
            scrollbar-width: thin;
            scrollbar-color: $primary-default #e9f3fc;
            &::-webkit-scrollbar {
                width: 7px;
            }

            &::-webkit-scrollbar-track {
                background: transparent;
            }

            &::-webkit-scrollbar-thumb {
                background-color: $primary-default;
                border-radius: 5px;
            }
        }

        & .bkt-collapse__button {
            border-radius: 10px;
            width: 34px;
            height: 34px;
            padding: 10px;
        }
    }

    & .bkt-search__input {
        background-color: transparent;
    }
}

@media (max-width: 576px) {
    .bkt-monitoring {
        gap: 15px;
        &__menu {
            flex-flow: column-reverse;

            & .bkt-button__new-monitoring {
                //margin-bottom: 2rem;
                height: 54px;
                align-items: center;
                display: flex;
                justify-content: center;
                border-right: 18px;
            }
        }

        & .bkt-button__new-monitoring {
            margin-bottom: 10px;
            height: 54px;
            align-items: center;
            display: flex;
            justify-content: center;
            border-right: 18px;
        }

        & .bkt-border-rounded {
            border-radius: 15px;
        }

        & .bkt-border-bottom-rounded-none {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }

        & .bkt-btn-monitoring {
            height: 44px;

            & .bkt-arrow {
                right: 10px;
                height: 34px;
                width: 34px;
                align-items: center;
                display: flex;
                justify-content: center;
            }
        }

        & .bkt-menu-monitoring {
            border-radius: 0 0 15px 15px;

            & .bkt-arrow {
                height: 34px;
                width: 34px;
                align-items: center;
                display: flex;
                justify-content: center;
            }

            & .bkt-menu-monitoring-list {
                &__item {
                    height: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-weight: 600;
                }
            }
        }

        //& .bkt-search {
        //    background-color: $body-bg;
        //}

        & .bkt-search__button {
            min-width: 44px;
            display: flex;
            justify-content: center;
            align-items: center;
            //margin-right: 7px;
        }
    }
}
